<template>
  <div style="height: 100vh;">
    <van-nav-bar :title="name" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    <iframe :src="url" seamless width="100%" frameborder="no" scrolling="auto" id="lw" style="height: calc(100% - 46px);"></iframe>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        url:null,
        name:null,
      }
    },
    created(){
      this.name = this.$route.query.name;
      this.url = this.$route.query.url;
    }
  }
</script>

<style scoped>
  #lw{
    width: 100%;
  }
  #lw::-webkit-scrollbar {
    display: none;
  }
</style>
